.btn {
  display: block;
  font-family: "Avenir Next";
  font-size: 0.75rem;
  padding: 1.25em 2em;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  background: linear-gradient(180deg, #F40C88 0%, #E10179 100%),
    linear-gradient(180deg, #F40C88 0%, #E10179 100%),
    linear-gradient(180deg, #F30B88 0%, #E00179 100%);
  border-radius: 2.5em;
  border: none;
  transition: all 0.24s;
  cursor: pointer;

  &.--secondary {
    background: #F30383;
    border-radius: 0.25rem;
  }
}