.tasks {
  position: relative;
  &__content {
    position: sticky;
    top: 2rem;
  }

  &__title {
    font-weight: 700;
    font-size: 1.5rem;
  }

  &__title-date {
  }

  &__wrapper {
    margin-top: 1rem;
    padding: 2rem 2.25rem;
    border-radius: 1rem;
    background-color: #242736;
    box-shadow: 4px 4px 24px rgba(26, 24, 34, 0.24);
    @media screen and (max-width: 1023px) {
      padding: 1rem;
    }
  }

  &__subtitle {
    font-weight: 500;
    font-size: 1.25rem;
    color: #7f8295;
  }

  &__list {
    margin-top: 1.25rem;
    list-style: none;
    margin-left: 0;
    padding: 0;
  }

  &__list-item {
    & + & {
      margin-top: 1.25rem;
    }
  }

  &__item-label {
    display: block;
    padding-left: 2.25rem;
    position: relative;
    cursor: pointer;
  }

  &__check {
    display: block;
    height: 0;
    width: 0;
    opacity: 0;
    overflow: hidden;
  }

  &__check-body {
    display: inline-block;
    height: 1.25rem;
    width: 1.25rem;
    background-color: #525566;
    border-radius: 0.25rem;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    transition: background-color 0.25s;
    &::before {
      content: "";
      position: absolute;
      height: 70%;
      width: 70%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      opacity: 0;
      transition: opacity 0.25s;
    }
  }

  &__item-descr {
  }

  &__check:focus ~ &__check-body {
    outline: 1px solid #ffffff;
  }

  &__check:checked {
    & ~ .tasks__check-body {
      background-color: #f50485;
      &::before {
        opacity: 1;
      }
    }

    & ~ .tasks__item-descr {
      text-decoration: line-through;
    }
  }

  &__tooltip {
    position: relative;
    margin-left: 0.5rem;
  }

  &__tooltip-icon {
    display: block;
    padding: 0;
    border: none;
    border-radius: 50%;
    height: 1.25rem;
    width: 1.25rem;
    background-size: 105%;
    background-repeat: no-repeat;
    background-position: center;
    overflow: hidden;
  }

  &__tooltip-text {
    display: block;
    width: 10rem;
    overflow: hidden;
    position: absolute;
    bottom: calc(100% + 1rem);
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.25s;
    padding: 0.5rem;
    font-size: 0.75rem;
    background-color: #4f5565;
    border-radius: 0.5rem;
  }

  &__tooltip:hover &__tooltip-text,
  &__tooltip-icon:focus ~ &__tooltip-text {
    opacity: 1;
  }
}
