.cookie {
  position: fixed;
  bottom: 1.5rem;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% - 2rem);
  max-width: 62rem;
  color: #666464;
  background: #F7F7F7;
  box-shadow: 2px 2px 24px rgba(0, 0, 0, 0.08);
  border-radius: 5px;
  padding: 1.25rem;
  transition: all 0.24s;

  @media screen and (max-width: 639px) {
    flex-direction: column;
  }

  &__icon {
    height: 4rem;
    width: 4rem;
    object-fit: contain;

    @media screen and (max-width: 991px) {
      height: 2.5rem;
      width: 2.5rem;
    }
  }

  &__text {
    margin-left: 2rem;
    margin-right: auto;
    font-weight: 500;
    max-width: 38rem;

    @media screen and (max-width: 639px) {
      margin-left: auto;
      margin-right: auto;
      margin-top: 1.25rem;
    }
  }

  &__link {
    color: #F30384;
    text-decoration: underline;
  }

  &__btn {
    background-color: #E92D92;
    margin-left: 1rem;
    font-weight: 500;
    font-size: 1.25rem;
    padding: 0.5rem 3rem;

    @media screen and (max-width: 991px) {
      font-size: 1rem;
    }

    @media screen and (max-width: 639px) {
      margin-left: 0;
      margin-top: 1.25rem;
    }
  }
}