.form {
  background-color: #242736;
  padding: 1.25rem 1.5rem;
  border-radius: 0.25rem;
  font-size: 0.75rem;

  &__title {
    font-weight: 500;
    font-size: 1rem;
  }

  &__text {
    margin-top: 0.5rem;
  }

  &__steps {
    margin-top: 3.5rem;
    padding: 0 2rem;
    counter-reset: steps;
  }

  &__steps-item {
    list-style: none;
    counter-increment: steps;
    position: relative;
    flex: 0 0 3.75rem;
    text-align: center;

    &::before {
      content: counter(steps);
      position: absolute;
      height: 1.75rem;
      width: 1.75rem;
      bottom: calc(100% + 0.25rem);
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      background-color: #333946;
    }

    &.--current::before {
      background-color: #E92D92;
    }
  }

  &__switcher {
    margin-top: 1.5rem;
    background-color: #333946;
    border-radius: 1.25rem;
    overflow: hidden;
    position: relative;
  }

  &__switcer-item {
    flex: 0 1 50%;
    text-align: center;
    padding: 0.5em 1em;
    cursor: pointer;
  }

  &__switcher-input {
    display: block;
    height: 0;
    width: 0;
    overflow: hidden;
  }

  &__switcher-body {
    display: block;
    height: 100%;
    width: 50%;
    background-color: #E92D92;
    border-radius: 1.25rem;
    position: absolute;
    z-index: 0;
    top: 0;
    right: 0;
    transition: all 0.24s;
  }

  &__switcher-input:checked~&__switcher-body {
    right: 50%;
  }

  &__switcher-text {
    font-size: 1.25em;
    position: relative;
    z-index: 1;
  }

  &__gifts {
    margin-top: 1.25rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1em;
    // height: 0;
    overflow: hidden;
    transition: all 0.24s;
  }

  &__gifts-item {
    cursor: pointer;
    background-color: #dedfe1;
    padding: 1rem;
    border-radius: 1rem;
    position: relative;
  }

  &__gift-input {
    display: block;
    height: 0;
    width: 0;
    overflow: hidden;
  }

  &__gift-pic {
    display: block;
    width: 100%;
  }

  &__gift-checked {
    height: 1.5rem;
    width: 1.5rem;
    position: absolute;
    color: #dedfe1;
    background-color: #f40386;
    border-radius: 50%;
    top: 1rem;
    right: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 900;
    opacity: 0;
    transition: all 0.24s;
  }

  &__gift-input:checked~&__gift-checked {
    opacity: 1;
  }

  &__gift-name {
    display: block;
    text-align: center;
    max-width: 10rem;
    padding: 0.25rem;
    color: #1c1a1b;
  }

  &__inner {
    margin-top: 1.25rem;
  }

  &__inner-title {
    font-weight: 700;
    font-size: 1.25rem;
  }

  &__inner-text {
    margin-top: 0.25rem;
    font-weight: 300;
  }

  &__input-item {
    margin-top: 1rem;
    display: block;
    width: 100%;
  }

  &__input-name {
    display: block;
    font-weight: 300;
  }

  &__input {
    display: block;
    width: 100%;
    background-color: #313444;
    padding: 0.75rem 1rem;
    font-weight: 300;
    border: none;
    border-radius: 0.25rem;
    margin-top: 0.5rem;
    color: #ffffff;

    &::placeholder {
      color: #ffffff;
    }

    &:focus {
      outline: #1c1a1b;
    }
  }

  &__submit {
    margin: 1.5rem auto 0;
    min-width: 8.5rem;
  }

  &__gift-img {
    height: 100%;
    width: 100%;
    line-height: 0;
    object-fit: contain;
    object-position: center;
  }
}