.choice {
  padding: 5.5rem 6rem;

  @media screen and (max-width: 991px) {
    padding: 5.5rem 0;
  }

  &__title {
    font-size: 2.5rem;
    text-align: center;
    color: #121316;
    font-weight: 700;
  }

  &__inner {
    margin-top: 2rem;

    @media screen and (max-width: 767px) {
      flex-direction: column;
    }
  }

  &__item {
    flex: 0 0 19rem;
    background-color: #fefefe;
    border-radius: 0.75rem;
    box-shadow: 0.5rem 0.5rem 0.75rem rgba(0, 0, 0, 0.06);
    color: #666464;

    &+& {
      margin-left: 1.5rem;

      @media screen and (max-width: 767px) {
        margin-left: 0;
        margin-top: 1.5rem;
      }
    }
  }

  &__item-title {
    color: #ffffff;
    font-weight: 700;
    flex: 0 0 auto;
    width: 100%;
    padding: 1.25rem 2rem;
    border-radius: 0.75rem;
    text-align: center;
    background: linear-gradient(180deg, #B9B9B9 0%, #979797 100%);

    &.--rose {
      background: linear-gradient(180deg, #F40C89 0%, #E1027A 100%);
    }

    &+& {
      margin-left: 1rem;
    }
  }

  &__item-content {
    padding: 1.5rem 2rem 2rem;
    flex: 1 1 100%;
    width: 100%;
  }

  &__item-list {
    padding: 0;
    margin: 0 auto 0 0;
    list-style: none;
    max-width: 13rem;
  }

  &__item-list-part {
    position: relative;
    padding-left: 2.5rem;

    &::before {
      content: '';
      position: absolute;
      height: 1.5rem;
      width: 1.5rem;
      top: 0.25rem;
      left: 0;
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }

    &+& {
      margin-top: 1rem;
    }
  }

  &__item-price {
    font-size: 2rem;
    margin: auto auto 0;
  }

  &__item-btn {
    width: 100%;
    max-width: 12rem;
    margin: 1rem auto 0;
  }

  &__item-super {
    margin-top: 1rem;
    background: linear-gradient(180deg, #F40C88 0%, #E10179 100%),
      linear-gradient(0deg, #666464, #666464),
      #FFFFFF;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    font-weight: 500;
  }
}