.privacy {
  background-color: #f7f7f7;
  padding-bottom: 9.5rem;
  color: #242736;

  h1 {
    padding: 4rem 0;
    font-weight: 700;
    font-size: 2.25rem;
    text-transform: uppercase;
    color: #e92d92;
    text-align: center;
  }

  article+article {
    margin-top: 4rem;
  }

  h2 {
    font-weight: 700;
    font-size: 1.5rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #e92d92;
    margin-bottom: 1rem;
  }

  p {
    font-size: 1.5rem;
    font-weight: 500;
  }

  a {
    text-decoration: none;
    color: #e92d92;
  }

  ol {
    margin-top: 1.5rem;
    padding-left: 1rem;
    line-height: 1.75;
    font-weight: 500;

    li {
      padding-left: 0.5em;
    }
  }
}